import { Component, OnInit } from '@angular/core';
import { ActionSheetController } from '@ionic/angular';
import { AuthService } from '../../servicios/auth.service';
import { ActivatedRoute } from '@angular/router';
import { ModalController } from '@ionic/angular';
import { FormularioComponent } from '../formulario/formulario.component';

interface Servicios {
    icon:string;
    nombre:string;
    precio:number;
};

@Component({
  selector: 'app-inicio',
  templateUrl: './inicio.page.html',
  styleUrls: ['./inicio.page.scss'],
})
export class InicioPage implements OnInit {
  id=null;

  items : Servicios [] = [
    {
      icon : 'pin',
      nombre : 'Servicio 1',
      precio : 3000
    },
    {
      icon : 'pin',
      nombre : 'Servicio 2',
      precio : 4000
    },
    {
      icon : 'pin',
      nombre : 'Servicio 3',
      precio : 5000
    },
    {
      icon : 'pin',
      nombre : 'Servicio 4',
      precio : 20000
    },
    {
      icon : 'pin',
      nombre : 'Servicio 5',
      precio : 7000
    },
    {
      icon : 'pin',
      nombre : 'Servicio 6',
      precio : 10000
    },
    {
      icon : 'pin',
      nombre : 'Servicio 7',
      precio : 100000
    },
    {
      icon : 'pin',
      nombre : 'Servicio 8',
      precio : 13000
    },
    {
      icon : 'pin',
      nombre : 'Servicio 9',
      precio : 30000
    }
  ];

  constructor( private actionSheetController : ActionSheetController, private auth: AuthService, private activeRouter: ActivatedRoute, private modal:ModalController ) { }

  ngOnInit() {
    this.id = this.activeRouter.snapshot.paramMap.get('id');
  }

  openForm(id,servicio,precio:number){
    this.modal.create({
      component: FormularioComponent,
      componentProps: {
        id:id,
        servicio:servicio,
        precio:precio
      }
    }).then( (modal) => {
      modal.present()
    })
  }

  onlogout(){
    this.auth.logout();
  }

  async presentActionSheet() {
    const actionSheet = await this.actionSheetController.create({
      header: 'Opciones',
      buttons: [{
        text: 'Salir',
        role: 'destructive',
        icon: 'log-out',
        handler: () => {
          this.onlogout();
        }
      }]
    });
    await actionSheet.present();
  }

}
